.chess-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem;
  width: 100%;
  min-height: 100vh;
}
.dropdowns-container {
  display: flex;
  gap: 1rem;
  padding: 0;
  justify-content: flex-start;
  margin-bottom: 1rem;
}
/* Override the individual selector container padding */
.dropdowns-container .selector-container {
  padding: 0;
}
/* Ensure proper sizing of selects */
.dropdowns-container .select-control {
  min-width: 180px;
  margin: 0;
}
/* Disclosure Sections */
.fen-controls-disclosure,
.view-controls-disclosure,
.move-controls-disclosure,
.combined-controls-disclosure {
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  overflow: hidden;
  background: rgba(0, 0, 0, 0.1);
}

/* Attached header styling - removes top border radius for seamless connection */
.attached-header {
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 0 !important;
}
.disclosure-header {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1rem;
  background: transparent;
  border: none;
  color: white;
  cursor: pointer;
  font-size: 0.875rem;
  font-weight: 500;
  transition: background-color 0.2s ease;
  text-align: left;
}
.disclosure-header:hover {
  background: rgba(255, 255, 255, 0.05);
}
.disclosure-header:focus {
  outline: 2px solid #007bff;
  outline-offset: -2px;
}
.disclosure-chevron {
  transition: transform 0.2s ease;
  font-size: 0.75rem;
  color: #007bff;
}
.disclosure-chevron.expanded {
  transform: rotate(90deg);
}
.disclosure-title {
  font-weight: 600;
  color: #e2e8f0;
}
.disclosure-content {
  overflow: hidden;
  transition:
    max-height 0.3s ease,
    padding 0.3s ease;
  width: 100%;
  box-sizing: border-box;
}
.disclosure-content.collapsed {
  max-height: 0;
  padding: 0 1rem;
}
.disclosure-content.expanded {
  max-height: 500px;
  padding: 0 1rem 1rem 1rem;
}
/* View Controls Grid - Horizontal Layout */
.view-controls-grid {
  display: grid;
  grid-template-columns: repeat(5, 200px);
  gap: 1rem;
  width: 100%;
  box-sizing: border-box;
}
.view-controls-grid .selector-wrapper {
  margin: 0;
  width: 100%;
  box-sizing: border-box;
}
.view-controls-grid .select-control {
  width: 160px;
  min-width: 160px;
  max-width: 160px;
  margin: 0;
  box-sizing: border-box;
}
/* Remove old dropdowns container styles since it's now in sidebar */
.dropdowns-container {
  display: none;
}
/* Main Content Layout - Two Column Layout */
.main-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: "positional historical";
  gap: 2rem;
  align-items: stretch;
  width: 100%;
  height: 100%;
  overflow: hidden;
  padding-bottom: 8px; /* Space for resize handle */
}

.positional-section {
  grid-area: positional;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  min-height: 0; /* Important for flex children */
  box-sizing: border-box;
}

.positional-section .view-container {
  width: 100%;
  flex: 1;
  min-height: 0; /* Allow shrinking */
  overflow: auto;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
}

.historical-section {
  grid-area: historical;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  min-height: 0; /* Important for flex children */
  box-sizing: border-box;
}

.historical-section .view-container {
  width: 100%;
  flex: 1;
  min-height: 0; /* Allow shrinking */
  overflow: auto;
  box-sizing: border-box;
  display: flex;
  align-items: flex-start;
}
/* Responsive design */
@media (max-width: 1000px) {
  .chess-container {
    max-width: 100%;
    padding: 0.5rem;
  }

  .main-content {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    height: auto;
  }

  .positional-section {
    order: 1;
    width: 100%;
    min-height: 300px;
  }

  .historical-section {
    order: 2;
    width: 100%;
    min-height: 300px;
  }

  .view-controls-grid {
    grid-template-columns: 1fr;
    gap: 0.5rem;
  }
}
/* SelectStyle.css updates */
.selector-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* Change from center to flex-start */
}
.selector-label {
  color: white;
  font-weight: 500;
  margin-bottom: 0.5rem;
}
.selector-container {
  display: flex;
  justify-content: flex-start; /* Change from center to flex-start */
}
